.users__block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.users {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 10px;
}
.pages {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(19, 91, 145, 0.14);
    color:#135B91;
    font-size: 18px;
    width: 250px;
    height: 40px;
    border-radius: 20px;
    margin-bottom: 20px;
}
.pages>*:not(:last-of-type) {
    margin-right: 2px;
}
.user__block {
    margin: 5px 0;
    flex: 0 0 49%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
    background-color: #fff;
    box-shadow: 0 20px 90px rgba(205, 217, 224, 0.5);
    border-radius: 30px;
}

.user__block > * {
    margin-bottom: 10px;
}

.avatar {
    display: inline-flex;
    height: 70px;
    width: 70px;
    border: 1px solid white;
}

.avatar__img {
    border-radius: 50%;
    width: 100%;
    height: 100%;
}

.name {
    font-size: 20px;
    font-weight: 500;
}

.status {
    padding: 5px 15px;
    background-color: rgba(233, 233, 255, 0.66);
    border-radius: 10px;
}

.location {
    margin-bottom: 20px;
}

.btn__more {
    margin-top: 20px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0 20px 90px rgba(158, 175, 183, 0.71);
    color: #000;
}

.selectedPage {
    font-weight: 700;
}
